<template>
  <div class="CopClass">
    <el-table :data="msg" border style="width: 100%">
      <el-table-column prop="id" label="序号" width="100px"/>
      <el-table-column prop="logdesc" label="内容"/>
      <el-table-column prop="makedate" label="日期"/>
      <el-table-column prop="maketime" label="时间"/>
    </el-table>
    <el-button @click="addUpdateLog()">新增更新日志</el-button>
  </div>
</template>

<script>
import 'element-ui'
import Vue from 'vue'
export default {
  name: 'CopName',
  data () {
    return {
      msg: []
    }
  },
  mounted: async function () {
    const response = await Vue.axios.get('/api/updatelog')
    this.msg = response.data
  },
  methods: {
    addUpdateLog: function () {
      this.$router.push({
        path: '/submitupdatelog'
      })
    }
  }
}
</script>



