<template><div>
<el-container>
  <el-header>Header</el-header>
  <el-container>
    <el-aside width="200px">Aside</el-aside>
    <el-container>
      <el-main>
        <el-row>
          <el-col :span="24" v-for="title in msg" v-bind:key="title.id"><div @click="test(title.desc)" ><h1> {{ title.title }} </h1></div></el-col>
        </el-row>
      </el-main>
      <el-footer><div @click="test1()">更新记录</div></el-footer>
    </el-container>
  </el-container>
</el-container>
</div></template>
<style>
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  .el-main {
    color: #333;
    text-align: center;
    line-height: 160px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
</style>
<script>
import 'element-ui'
import Vue from 'vue'
export default {
  data () {
    return {
      msg: []
    }
  },
  mounted: async function () {
    const response = await Vue.axios.get('/api/11217')
    this.msg = response.data
  },
  methods: {
    test: function (id) {
      this.$router.push({
        path: '/enter',
        query: {
          id: id
        }
      })
    },
    test1: function () {
      this.$router.push({
        path: '/updatelog'
      })
    }
  }
}
</script>



