import Vue from 'vue'
import Router from 'vue-router'
import CopClass from '../components/EnterPage'
import Test from '../components/test'
import UpdateLog from '../components/UpdateLog'
import SubmitUpdateLog from '../components/SubmitUpdateLog'

Vue.use(Router)

export default new Router({
  mode: 'history', // 解决去掉URL自动带#问题，但是打包会出现问题
  routes: [
    {
      path: '/',
      name: 'Test',
      component: Test
    },
    {
      path: '/enter',
      name: 'CopClass',
      component: CopClass
    },
    {
      path: '/updatelog',
      name: 'UpdateLog',
      component: UpdateLog
    },
    {
      path: '/submitupdatelog',
      name: 'SubmitUpdateLog',
      component: SubmitUpdateLog
    }
  ]
})



