<template>
  <div>
    更新日志内容：<el-input style="width: auto" id="LogDesc" v-model="LogDesc"/><br>
    更新日志日期：<el-date-picker type="date" placeholder="选择日期" value-format="yyyy-MM-dd" id="LogData" v-model="LogData"/><br>
    更新日志时间：<el-time-picker placeholder="任意时间点" value-format="HH:mm:ss" format="HH:mm:ss" id="LogTime" v-model="LogTime"/><br>
    <el-button @click="SubmitUpdateLog()">提交</el-button>
    <el-button @click="BackUpdateLog">返回</el-button>
  </div>
</template>

<script>
import 'element-ui'
import Vue from 'vue'
export default {
  data () {
    return {
      LogDesc: '',
      LogData: '',
      LogTime: ''
    }
  },
  methods: {
    SubmitUpdateLog: async function () {
      if (this.LogDesc == null) {
        alert('描述不能为空')
        return
      }
      const response = await Vue.axios.get('/api/submitupdatelog', {
        params: { LogDesc: this.LogDesc, LogData: this.LogData, LogTime: this.LogTime }})
      // eslint-disable-next-line eqeqeq
      if (response.data.code == '000000') {
        alert('成功')
      }
    },
    BackUpdateLog: function () {
      this.$router.push({
        path: '/updatelog'
      })
    }
  }
}
</script>



