<template>
    <div class="CopClass">
      <p>{{ msg }}</p>
    </div>
</template>

<script>
import 'element-ui'
export default {
  name: 'CopName',
  data () {
    return {
      msg: this.$route.query.id
    }
  }
}
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
</style>



